import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Semaphore from '../views/Semaphore.vue'
import Telegraph from '../views/Telegraph.vue'
import Telepathy from '../views/Telepathy.vue'
import Subspace from '../views/Subspace.vue'
import PostRiders from '../views/PostRiders.vue'
import Radio from '../views/Radio.vue'
import POTS from '../views/POTS.vue'
import Mobile from '../views/Mobile.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/mobile',
    name: 'Mobile',
    component: Mobile
  },
  {
    path: '/radio',
    name: 'Radio',
    component: Radio
  },
  {
    path: '/POTS',
    name: 'POTS Telephony',
    component: POTS
  },
  {
    path: '/semaphore',
    name: 'Semaphore',
    component: Semaphore
  },
  {
    path: '/telegraph',
    name: 'Telegraphy',
    component: Telegraph
  },
  {
    path: '/telepathy',
    name: 'Telepathy',
    component: Telepathy
  },
  {
    path: '/subspace',
    name: 'Subspace',
    component: Subspace
  },
  {
    path: '/thurn-taxis',
    name: 'Thurn und Taxis',
    component: PostRiders
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
